<template>
<div class="result">
  <div class="liners">
    <h2>您的测试结果已经生成</h2>
    <img src="../../../assets/question/sure.png" class="success_img">
    <p>
      
    </p>
    <div class="nextstep" @click="nextstep">
      查看完整报告
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'result',
  data: function() {
    return {}
  },
  methods: {
    nextstep() {
      this.$router.replace({
        name: 'questionreport'
      })
    }
  }
}
</script>

<style scoped lang="less">
@import '../../../styles/mixin';
.result {
  #wh(100%, 100%);
  background-image: linear-gradient(135deg, #04A2FC 0%, #401CD5 100%);
  .liners {
    #wh(100%, 100%);
    padding-top: 150/@rem;
    background-size: 100%;
    background-repeat: no-repeat;
    background-image: url('../../../assets/question/bg.png');
    h2 {
      #wh(100%,
      55/@rem);
      text-align: center;
      #sc(48/@rem,
      #fff);
    }
    .success_img {
      #wh(228/@rem,
      228/@rem);
      display: block;
      margin: 72/@rem auto 0;
      background-size: 100%;
      background-repeat: no-repeat;
    }
    p {
      height:400/@rem;
    }
    .nextstep {
      #wh(440/@rem,
      68/@rem);
      background-color: #FFFFFF;
      border-radius: 34/@rem;
      text-align: center;
      #sc(30/@rem,
      #1E2FBE);
      line-height: 68/@rem;
      margin:0 auto;
    }
  }
}
</style>


